.container {
  display: flex;
  flex-direction: column;
}
.container :global(.uui-input) {
  display: block;
  width: 100%;
  min-width: 0;
  outline: none;
  border-width: 1px;
}
.container :global(.uui-disabled), .container :global(.uui-readonly) {
  resize: none;
  overflow-y: auto;
}

.auto-size {
  resize: none;
  overflow-y: hidden;
}

.no-auto-size {
  resize: vertical;
  overflow: auto;
}